  data: () => ({
    isActive: false,
    hasError: true,
    menu: true,
    activeTab: "0",
    sheet: false,
    shoppingCart: null,
    age: null,
    active: false,
    qtd: ["1 item", "2 item", "3 item", "4 item"],
    items: ["Dinheiro", "Cartão de crédito", "Cartão de débito"],
    model: [],
    windowSize: {
      x: 0,
      y: 0
    },
    sizeWidht: 100
  }),

  methods: {
    next() {
      this.activeTab = 1;
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
      if (this.windowSize <= 500) {
        this.isActive = true;
      }
    }
  },

  created() {
    window.addEventListener("resize", () => {
      if (this.windowSize.x <= 500) {
        this.isActive = true;
      } else {
        this.isActive = false;
      }
    });

    this.onResize();
  }
};
</script>
